.price {
  padding-top: 60px;
  &__title {
    position: relative;
    text-align: center;
    @include circle;
    &:after {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  &__wrapper {
    padding: 0 60px;
    margin-top: 60px;
    display: grid;
    grid-template: repeat(3, minmax(45px, auto)) / repeat(2, 365px);
    grid-auto-rows: minmax(45px, auto);
    row-gap: 15px;
    justify-content: space-between;
  }
  &__item {
    position: relative;
    padding-left: 25px;
    display: grid;
    grid-template: repeat(2, minmax(21px, auto)) / repeat(2, auto);
    row-gap: 5px;
    &-title,
    &-price {
      font-weight: 700;
      font-size: 14px;
      line-height: 21px;
      color: #000000;
    }
    &-value {
      justify-self: end;
    }
    &-descr {
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      color: #000000;
      grid-column: 1/3;
    }
    &:before {
      content: '';
      width: 15px;
      height: 15px;
      position: absolute;
      top: 3px;
      left: 0;
      background-color: var(--main-color);
      border-radius: 100%;
    }
  }
}
