@media(max-width: 1439px) {
  .sidepanel {
    display: none;
  }
}

@include for-desktop {
  .container {
    max-width: 960px
  }
  .sidepanel {
    display: none;
  }

  .about {
    &__inner {
      grid-template-columns: 400px 520px;
      column-gap: 40px;
    }
    &__attainments {
      grid-template: repeat(2, minmax(115px, auto), repeat(2, 250px));
      column-gap: 20px;
    }
    .attainments {
      &__item {
        column-gap: 10px;
        grid-template-columns: 40px 195px;
      }
    }
  }

  .resume {
    &__wrapper {
      padding: 0;
      margin-top: 60px;
      grid-template-columns: repeat(2, 415px);
      column-gap: 100px;
    }
  }

  .skills {
    &__wrapper {
      grid-template-columns: repeat(3, 300px);
    }
  }

  .portfolio {
    &__wrapper {
      grid-template-columns: repeat(3, 300px);
      gap: 15px;
    }
    &__item {
      &--horizontal {
        grid-column: unset;
        grid-row: unset;
      }
      &--vertical {
        grid-row: unset;
        grid-column: unset;
      }
      .image {
        width: 100%;
        height: 100%;
      }
    }
  }
}

@include big-tablet {
  .container {
    max-width: 720px
  }
  .title {
    &--fz36 {
      font-size: 30px;
    }
  }
  .about {
    &__inner {
      grid-template-columns: 300px 380px;
    }
    &__attainments {
      grid-template-columns: 380px;
      grid-auto-rows: minmax(115px, auto);
      row-gap: 30px;
    }
    .attainments {
      &__item {
        column-gap: 20px;
        grid-template-columns: 40px 320px;
      }
    }
  }

  .resume {
    &__wrapper {
      grid-template-columns: repeat(2, 350px);
      column-gap: 20px;
    }
  }

  .skills {
    &__wrapper {
      grid-template-columns: repeat(2, 339px);
      row-gap: 20px;
    }
  }

  .portfolio {
    &__wrapper {
      grid-template-columns: repeat(2, 340px);
      gap: 10px;
    }
  }

  .price {
    &__wrapper {
      grid-template-columns: 1fr;
    }
    &__item {
      &:not(:first-child) {
        padding-top: 10px;
        border-top: 1px solid #e3e3e3;
        &:before {
          top: 15px;
        }
      }
    }
  }
}

@include tablet {
  .container {
    max-width: 540px
  }
  .title {
    &--fz48 {
      font-size: 28px;
    }
  }
  .hero {
    &__title {
      width: 300px;
      line-height: 39px;
      font-size: 26px;

    }
  }
  .about {
    padding-top: 40px;
    &__inner {
      grid-template: 1fr auto auto/ 1fr;
    }
    &__img {
      text-align: center;
    }
    .attainments {
      grid-template-columns: repeat(2, 260px);
      &__item {
        column-gap: 20px;
        grid-template-columns: 40px 200px;
      }
    }
  }
  .resume {
    padding-top: 40px;
    &__wrapper {
      margin-top: 40px;
      grid-template-columns: 1fr;
      grid-auto-rows: 1fr;
      row-gap: 40px;
      column-gap: 20px;
    }
  }

  .skills {
    &__wrapper {
      grid-template-columns: repeat(2, 248px);
    }
  }

  .portfolio {
    &__wrapper {
      grid-template-columns: repeat(2, 250px);
    }
  }
}

@include mobile {
  .container {
    max-width: 100%;
    padding: 0 10px;
  }
  .title {
    &--fz48 {
      font-size: 26px;
    }
    &--fz36 {
      font-size: 24px;
    }
  }
  .hero {
    &__hamburger {
      right: 20px;
    }
    &__content {
      padding-top: 206px;
    }
    &__btns {
      margin-top: 32px;
    }
  }

  .menu {
    &__block {
      padding-left: 20px;
      width: 250px;
    }
  }

  .about {
    &__subtitle {
      line-height: 36px;
    }
    &__title {
      margin-top: 20px;
    }
    &__inner {
      grid-template: 340px auto auto / 1fr;
    }
    .attainments {
      grid-template-columns: 1fr;
    }
  }

  .skills {
    padding-top: 40px;
    &__wrapper {
      grid-template-columns: 1fr;
    }
  }

  .portfolio {
    padding-top: 40px;
    &__wrapper {
      margin-top: 40px;
      grid-template-columns: 1fr;
      row-gap: 20px;
    }
  }

  .price {
    &__wrapper {
      padding: 0;
    }
  }
}
